.Loading__dot:after {
  content: '.';
  margin: 0 2px;
  -webkit-animation: dots 2s infinite linear;
          animation: dots 2s infinite linear;
}

.Loading__dot_pos_2:after {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.Loading__dot_pos_3:after {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes dots {
  0%,
  25% {
    opacity: 1;
  }
  26%,
  100% {
    opacity: 0;
  }
}

@keyframes dots {
  0%,
  25% {
    opacity: 1;
  }
  26%,
  100% {
    opacity: 0;
  }
}



